import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import VModal from 'vue-js-modal'
Vue.use(VModal);

import mixin from './mixin'
Vue.mixin(mixin);

import loader from './loader'
Vue.mixin(loader);

new Vue({
  router,
  store,
  el: '#app',
  render: h => h(App)
})
